<template>
	<view class="match-page">
		<view class="tab-header">
			<view class="head-item" :class="navIndex==index?'navActive': ''" v-for="(item,index) in navList"
				:key='index' @click="checkIndex(index)">{{item.text}}
			</view>
		</view>
		<view class="match-nav">
			<!-- <view class="match-pic">
				
			</view> -->
			<image :src="matchInfo.pic" class="match-pic"></image>
			<view class="match-explain">
				{{matchInfo.dec}}
			</view>
		</view>
		<view class="content" id='content'>
			<swiper class="swiper" circular autoplay="false" interval="2000" duration="500">
				<swiper-item>
					<view class="swiper-item ">
						<list-bar :listInfo='contenInfo' :hasLeftTag='true' :hasRightTag='true'></list-bar>

					</view>

				</swiper-item>

			</swiper>
		</view>



		<list-bar></list-bar>
	</view>
</template>

<script>
	import listBar from '@/components/common/list-bar/list-bar.vue'
	export default {
		data() {
			return {
				navList: [{
						url: '/static/c1.png',
						badge: '0',
						type: "primary",
						text: '自行车'
					},
					{
						url: '/static/c2.png',
						badge: '1',
						type: "success",
						text: '健步走'
					},
					{
						url: '/static/c3.png',
						badge: '99',
						type: "warning",
						text: '广场舞'
					},
					{
						url: '/static/c4.png',
						badge: '2',
						type: "error",
						text: '企业排舞'
					},
				],
				old: {
					scrollTop: 0
				},
				matchInfo: {},
				contenInfo: [],
				navIndex: 0,
				headHeight: 54,
				swiperHeight: 0
			}
		},
		components: {
			listBar
		},
		created() {
			// const content = uni.createSelectorQuery().select('.content');
			// content.boundingClientRect(info => {
			// 	console.log('info', info)
			// })
			this.checkIndex(this.navIndex)
			this.init(this.navIndex)
		},
		methods: {
			init(index) {
				this.$http.request({
					url: 'home/hotActivity'
				}, 'get').then(res => {
					if (res.project) {
						(res.project).forEach(item => {


							item.title = this.navList[index].text + '-' + item.title;
							item.tagRightText = item.mode ? '线上' : '线下';
							item.tagText = item.raise == '0' ? '未开始' : (item.raise == '1' ? '已结束' :
								'报名中');
							item.tagRightType = item.mode;
							return item;
						})

						this.contenInfo = res.project;
					}
					this.matchInfo = res;

				})
			},
			scroll: function(e) {
				this.old.scrollTop = e.detail.scrollTop
			},
			checkIndex(index) {
				this.navIndex = index;
				this.init(index)
			},
			seSwiperHeight() {
				const content = uni.createSelectorQuery().select('.content').boundingClientRect()
				let header = uni.createSelectorQuery().select('.tab-header').boundingClientRect();
				var height = 0;
				header.exec(res => {

					if (res && res[0]) {
						height = uni.getSystemInfoSync().screenHeight - res[0].height;
					}
				})


				content.exec(res => {
					if (res && res[0]) {
						this.swiperHeight = height;
					}
				})
			},
			tabChange(e) {
				let contentIndex = e.detail.current;
				this.navIndex = contentIndex;

				// this.contenInfo = this.navList[contentIndex].badge
			}
		}
	}
</script>

<style lang="scss" scoped>
	.match-page {
		width: 100%;
		display: flex;
		flex-direction: column;

		.match-pic {
			width: 100%;
			height: 400rpx;
		}

		.match-explain {
			width: 100%;
			box-sizing: border-box;
			padding: 10rpx 20rpx;
			text-indent: 60rpx;
		}
	}

	.tab-header {
		width: 100%;
		height: 54rpx;
		display: flex;
		background-color: #fff;
		// position: fixed;
		// z-index: 10;

		.head-item {
			flex: 1;
			text-align: center;
		}

		.navActive {
			border-bottom: 2rpx solid palegreen;
		}
	}

	.swiper {
		width: 100%;
		overflow-y: scroll;
		height: calc(100vh - 600rpx);
		margin-bottom: 100rpx;
	}

	.content {
		flex: 1;
		height: calc(100vh - 600rpx);
		margin-bottom: 100rpx;
		margin-top: 54rpx;
		// width: 100%;
		// height: 500px;
		// background-color: pink;

	}
</style>
